.wwy-body{
	width: 35.175rem;
	height: 17.25rem;
	background: rgba(52,126,212,0.12);
	border: 0.025rem solid rgba(9,185,255,0.20);
	box-shadow: inset 0 0 0.3rem 0.1rem rgba(74,144,226,0.39);
	margin: 0.475rem auto 0;
	.wwy-part1{
		.wwy1-head{
			background: url('../img/body-head.png') no-repeat;
			width: 100%;
			height: 1.0rem;
			line-height: 1rem;
			margin: 0 auto;
			-webkit-background-size: 100% 100%;
			background-size: 100% 100%;
			i{
				display: inline-block;
				background: url('../img/person.png') no-repeat;
				width: 0.5rem;
				height: 0.375rem;
				vertical-align: middle;
				-webkit-background-size: 100% 100%;
				background-size: 100% 100%;
				margin-left: 0.375rem;
				margin-right: 0.35rem;
			}
			span{
				font-size: 0.375rem;
				font-family: PingFangSC-Medium;
				color: #fff;
			}
		}
		.wwy1-body{
			height:0.9875rem;
			line-height: 0.9875rem;

			span{
				font-family: PingFangSC-Medium;
				font-size: 0.35rem;
				color: #FFFFFF;
			}
			.span-1{
				margin-left: 0.65rem;
				margin-right: 0.25rem;
			}
			input{
				width: 4.05rem;
				height: 0.675rem;
				background: rgba(39,140,182,0.13);
				border: 0.025rem solid #3D92B6;
				font-family: PingFangSC-Regular;
				font-size: 0.375rem;
				color: #5DC2EA;
				padding-left: 0.325rem;

			}
			:-ms-input-placeholder { /* Internet Explorer 10+ */
			  color: #5DC2EA;
			  font-size: 0.375rem;
			} 
			::-webkit-input-placeholder { /* WebKit browsers */
			  color:#5DC2EA;
			  font-size: 0.375rem;
			}  
			.span-2{
				margin-left: 1.55rem;
				margin-right: 0.4rem;
			}
			.span-3{
				margin-left: 1.775rem;
				margin-right: 0.25rem;
			}
			.input-cla{
				display: inline-block;
				position: relative;
			}
			.icon-1{
				position: absolute;
				background: url('../img/date.png') no-repeat center;
				width: 0.4rem;
				height: 0.4rem;
				/* line-height: 0.7rem; */
				-webkit-background-size: 100% 100%;
				background-size: 100% 100%;
				top: 0.29rem;
    			left: 3.8rem;
			}

			.icon-2{
				display: inline-block;
				background: url('../img/chaxun1.png') no-repeat center;
				width: 1.875rem;
				height: 0.7rem;
				line-height: 0.7rem;
				font-family: PingFangSC-Medium;
				font-size: 0.375rem;
				color: #FFFFFF;
				-webkit-background-size: 100% 100%;
				background-size: 100% 100%;
				margin-left:2.3rem;
				text-align: center;
				border: none;
				outline: none;
				cursor: pointer;
			}
		}
	}
	.wwy-part2{
		width:99.7%;
		margin-left: 0.1rem; 
		tr{
			font-family: PingFangSC-Regular;
			font-size: 0.35rem;
			color: #FFFFFF;
			border-bottom: 0.025rem solid rgba(235,247,252,0.32) ;
			th{
				height:0.995rem;
				line-height:0.995rem;
				background-color: rgba(63,133,244,0.18);
				text-align: center;
				color:#fff; 
			}
			.th1{
				margin-left:0.65rem;
				margin-right: 0.75rem;
			}
			.th2{
				
				margin-right: 0.85rem;
			}
			.th3{
				margin-right: 1.55rem;
			}
			.th4{
				margin-right: 2.625rem;
			}
			.th5{
				margin-right: 2.5rem;
			}
			.th6{
				margin-right: 2.125rem;
			}
			.th7{
				margin-right: 2.2rem;
			}
			.th8{
				margin-right: 1.8rem;
			}
			.th8{
				margin-right: 3.95rem;
			}
			td{
				height:0.975rem;
				line-height:0.975rem;
				text-align: center;
				.icon-1{
					display: inline-block;
					background: url('../img/dianzi.png') no-repeat ;
					width: 1.6rem;
					height: 0.7rem;
					line-height: 0.7rem;
					-webkit-background-size: 100% 100%;
					background-size: 100% 100%;
				}
				.icon-2{
					display: inline-block;
					background: url('../img/tuwen.png') no-repeat ;
					width: 1.6rem;
					height: 0.7rem;
					line-height: 0.7rem;
					-webkit-background-size: 100% 100%;
					background-size: 100% 100%;
				}
			}
		}
	}
	.wwy-part3{
		height:1rem;
		line-height: 1rem;
		display: inline-block;
		li{
			/* height: 0.5rem; */
			/* height:1rem;
			line-height: 1rem; */
			display: inline-block;
			text-align: center;
			font-family: PingFangSC-Regular;
			font-size: 0.3rem;
			color: #FFFFFF;
			cursor: pointer;
		}
		.li-cla{
			width: 0.5rem;
			height: 0.5rem;
			line-height: 0.5rem;
			background: rgba(0,76,166,0.57);
			border: 0.025rem solid #107F8C;
			margin-top: 0.3rem;
			margin-right:0.2rem; 

		}
		.fir{
			margin-left: 4.25rem;
		}
		.fin{
			height: 0.5rem;
			line-height: 0.5rem;
			font-family: PingFangSC-Regular;
			font-size: 0.3rem;
			color: #FFFFFF;
			margin-right: 0.2rem;
		}
		.fins{
			margin-left: 0.075rem;
		}


	}
	
}