.cloud-wraper{
    width: 100%;
    min-height: 20.025rem;
    position: absolute;
    top: 0;
    bottom: 0;
    .cloud-top{
        width: 100%;
        height: 1.95rem;
        background: url('../img/top-icon.png') no-repeat;
        background-size: 100% 100%;
        .top-rt{
            float: right;
            font-size: 0;
            vertical-align: middle;
            margin: 0.65rem 0.55rem 0 0;
            i{
                display: inline-block;
                vertical-align: middle;
                cursor: pointer;
            }
            .first-page{
                width: 0.4rem;
                height: 0.375rem;
                background: url('../img/first-page.png') no-repeat;
                background-size: 100% 100%;
            }
            .all-screem{
                width: 0.35rem;
                height: 0.35rem;
                background: url('../img/all-screem.png') no-repeat;
                background-size: 100% 100%;
                margin-left: 0.2rem;
            }
            span{
                font-family: 'PingFangSC-Regular';
                font-size: 0.35rem;
                color: #FFFFFF;
                display: inline-block;
                vertical-align: middle;
                padding: 0 0.2rem;
                line-height: 0.5rem;
                cursor: pointer;
            }
            .rt-lines{
                width: 0.05rem;
                height: 0.275rem;
                background: url('../img/rt-lines.png') no-repeat;
                background-size: 100% 100%;
                background-color: #FFFFFF;
            }
            .quit-sys{
                width: 0.325rem;
                height: 0.35rem;
                background: url('../img/quit-sys.png') no-repeat;
                background-size: 100% 100%;
                margin-left: 0.2rem;
            }
        }
    }
    .cloud-cons{
        .cloud-mid-cons{
            .cloud-mid-top{
                width: 14.5rem;
                height: 13.5rem;
                // display: inline-block;
                background: url('../img/cloud-mid-icon.png') no-repeat;
                background-size: 100% 100%;
                margin: 0.45rem auto 0.35rem auto;
                .mid-top-cons{
                    padding: 0.35rem;
                    .cons-header{
                        height: 0.85rem;
                        text-align: center;
                        padding-bottom: 0.3rem;
                        border-bottom: 0.05rem solid #B8DAE6;
                        .total-num{
                            display: inline-block;
                            margin-left: 0.35rem;
                            p{
                                font-family: 'PingFangSC-Regular';
                                font-size: 0.35rem;
                                color: #FFFFFF;
                                text-align: left;
                                display: inline-block;
                                vertical-align: bottom;
                            }
                        }
                       .add-num{
                           margin-left: 0rem;
                       }
                        .cons-header-lf{
                            float: left;
                            margin-left: 0.2rem;
                            .bd{
                                display: inline-block;
                                vertical-align: bottom;
                                ul{
                                    li{
                                        .data-numlf{
                                            height: 0.875rem;
                                            display: inline-block;
                                            line-height: 0.875rem;
                                            font-family: 'reducto_condensed_ssiCn';
                                            font-size: 0.8rem;
                                            color: #57D4FF;
                                            display: inline-block;
                                            vertical-align: bottom;
                                            div{
                                                display: inline-block;
                                                width: 0.65rem;
                                                height: 0.875rem;
                                                line-height: 0.875rem;
                                                background: rgba(52, 170, 217, 0.3);
                                                margin: 0 -0.025rem;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                       .cons-header-rt{
                            float: right;
                            margin-right: 0.65rem;
                            .bd{
                                display: inline-block;
                                vertical-align: bottom;
                                ul{
                                    li{
                                        .data-numrt{
                                            height: 0.875rem;
                                            display: inline-block;
                                            line-height: 0.875rem;
                                            font-family: 'reducto_condensed_ssiCn';
                                            font-size: 0.8rem;
                                            color: #57D4FF;
                                            display: inline-block;
                                            vertical-align: bottom;
                                            div{
                                                display: inline-block;
                                                width: 0.65rem;
                                                height: 0.875rem;
                                                line-height: 0.875rem;
                                                background: rgba(52, 170, 217, 0.3);
                                                margin: 0 -0.025rem;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .cons-tit{
                        text-align: center;
                        margin-top: 0.1875rem;
                        .data-icon{
                            width: 0.4rem;
                            height: 0.4rem;
                            display: inline-block;
                            vertical-align: middle;
                            background: url('../img/data-icon.png') no-repeat center center;
                            background-size: 100% 100%;
                            margin-right: 0.2rem;
                        }
                        p{
                            font-family: 'PingFangSC-Medium';
                            font-size: 0.4rem;
                            color: #FFFFFF;
                            line-height: 0.55rem;
                            display: inline-block;
                            vertical-align: middle;
                        }
                    }
                    .cons-main{
                        position: relative;
                        .map-top{
                            p{
                                position: absolute;
                                z-index: 99;
                                top: 0.9rem;
                                right: 0.5rem;
                                text-align: center;
                                font-family: 'PingFangSC-Regular';
                                font-size: 0.3rem;
                                color: #FFFFFF;
                            }
                            .hos-center{
                                position: absolute;
                                z-index: 99;
                                top: -0.95rem;
                                right: 0.5rem;
                                width: 2.65rem;
                                height: 1.8rem;
                                display: inline-block;
                                vertical-align: middle;
                                background: url('../img/hos-center.png') no-repeat;
                                background-size: 100% 100%;
                            }
                        }
                        .echarts-map{
                            width: 100%;
                            height: 11.5rem;
                            margin: 0.95rem auto 0 auto;
                        }
                    }
                }
            }
            .cloud-mid-bot{
                width: 14.5rem;
                height: 3.4rem;
                display: block;
                background: url('../img/cloud-mid-bot.png') no-repeat;
                background-size: 100% 100%;
                margin: 0 auto;
                position: relative;
                ul{
                    padding: 0.425rem 0 0.325rem 0;
                    display: flex;
                    justify-content: space-around;
                    font-size: 0;
                    li{
                        width: 33.3%;
                        text-align: center;
                        i{
                            width: 0.9rem;
                            height: 0.9rem;
                            display: inline-block;
                            vertical-align: middle;
                            margin-right: 0.3rem;
                        }
                        .li-icon1{
                            background: url('../img/li-icon1.png') no-repeat center center;
                            background-size: 100% 100%;
                            margin-left: 0.7rem;
                        }
                        .li-icon2{
                            background: url('../img/li-icon2.png') no-repeat center center;
                            background-size: 100% 100%;
                        }
                        .li-icon3{
                            background: url('../img/li-icon3.png') no-repeat center center;
                            background-size: 100% 100%;
                        }
                        .li-rt{
                            display: inline-block;
                            vertical-align: middle;
                            text-align: left;
                            // height: 2.6rem;
                            border-left: 1px solid rgba(255, 255, 255, 0.33);
                            .li-rt-top{
                                margin-left: 0.35rem;
                                
                                p{
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #2BBAEB;
                                }
                                .add-num{
                                    color: #7AC545;
                                }
                                .add-visit{
                                    color: #F2A526;
                                }
                                span{
                                    font-family: 'PingFangSC-Regular';
                                    font-size: 0.35rem;
                                    color: #FFFFFF;
                                    line-height: 0.5rem;
                                }
                                
                                .data-num1{
                                    width: 2.1rem;
                                    height: 0.85rem;
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #2BBAEB;
                                   
                                }
                                .data-num2{
                                    width: 2.1rem;
                                    height: 0.85rem;
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #2BBAEB;
                                   
                                }
                                .data-num3{
                                    width: 2.1rem;
                                    height: 0.85rem;
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #7AC545;
                                }
                                .data-num4{
                                    width: 2.1rem;
                                    height: 0.85rem;
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #7AC545;
                                }
                                .data-num5{
                                    width: 2.1rem;
                                    height: 0.85rem;
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #F2A526;
                                   
                                }
                                .data-num6{
                                    width: 2.1rem;
                                    height: 0.85rem;
                                    font-family: 'reducto_condensed_ssiCn';
                                    font-size: 0.75rem;
                                    line-height: 0.875rem;
                                    color: #F2A526;
                                }
                            }
                        }
                    }
                }
            }
        }
        /* left */
        .cloud-lf-cons{
            float: left;
        }
        /* right */ 
        .cloud-rt-cons {
            float: right;
        }
    }
    
}
/* 叫号页面 */
.call-wraper{
    width: 100%;
    min-height: 27rem;
    position: absolute;
    top: 0;
    bottom: 0;
    .call-top-header{
        height: 2.75rem;
        font-size: 0;
        margin-top: 0.15rem;
        .call-top-lf{
            float: left;
            width: 10rem;
            height: 1.725rem;
            background: url('../img/call-lf-icon.png') no-repeat;
            background-size: 100% 100%;
            margin: 0.6rem 0 0 1rem;
        }
        .call-top-mid{
            width: 19.325rem;
            height: 2.575rem;
            background: url('../img/call-header-top.png') no-repeat;
            background-size: 100% 100%;
            margin: 0 auto;
        }
        .call-top-rt{
            float: right;
            margin: 0.65rem 1.5rem 0 0;
            .rt-data{
                display: inline-block;
                vertical-align: middle;
                p{
                    font-family: 'PingFangSC-Regular';
                    font-size: 0.6rem;
                    color: #FFFFFF;
                    line-height: 0.825rem;
                    text-align: right;
                };
            }
            .rt-data-icon{
                width: 1.05rem;
                height: 1.05rem;
                display: inline-block;
                vertical-align: middle;
                background: url('../img/rt-data-icon.png') no-repeat;
                background-size: 100% 100%;
                margin: 0 0.375rem 0 0.575rem;
            }
            .data-time{
                display: inline-block;
                vertical-align: middle;
                p{
                    font-family: 'PingFangSC-Regular';
                    font-size: 1.05rem;
                    color: #FFFFFF;
                }
            }
        }
        
    }
    .call-nav-bar{
        width: 44.975rem;
        height: 2.85rem;
        background: url('../img/call-nav-bar.png') no-repeat;
        background-size: 100% 100%;
        margin: 0.625rem auto 0.5rem auto;
        .call-sound-icon{
            width: 1.25rem;
            height: 1.25rem;
            line-height: 2.85rem;
            display: inline-block;
            vertical-align: middle;
            background: url('../img/call-sound-icon.png') no-repeat;
            background-size: 100% 100%;
            margin: 0 0.375rem 0 0.75rem;
        }
        .bd{
            display: inline-block;
            vertical-align: middle;
            width: 40rem;
            height: 2.85rem;
            .tempWrap{
                width: 40rem !important;
                height: 2.85rem;
            }
            ul{
                height: 2.85rem;
                li{
                    height: 2.85rem;
                    display: inline-block;
                    vertical-align: middle;
                    font-family: 'PingFangSC-Semibold';
                    font-size: 1.5rem;
                    color: #00FFC6;
                    letter-spacing: 0.0563rem;
                    line-height: 2.85rem;
                    em{
                        margin-left: 1rem;
                    }
                }
            }
        }
    }
    .call-cons{
        width: 93.75%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        .call-cons-lf{
            width: 28.15rem;
            height: 17.1rem;
            display: inline-block;
            vertical-align: middle;
            background: url('../img/lf-cons-bg.png') no-repeat;
            background-size: 100% 100%;
            .cons-lf-tit{
                ul{
                    li{
                        font-family: 'PingFangSC-Semibold';
                        font-size: 0.95rem;
                        color: #FFFFFF;
                        letter-spacing: 0.0355rem;
                        line-height: 2.5rem;
                        text-align: center;
                        
                    }
                }
                
            }
            .bd{
                width: 28.15rem;
                height: 14.7rem;
                .tempWrap{
                    height: 2.45rem !important;
                }
                ul{
                    
                    li{
                        a{
                            display: inline-block;
                            width: 23%;
                            font-family: 'PingFangSC-Semibold';
                            font-size: 0.9rem;
                            color: #FFFFFF;
                            letter-spacing: 0.0338rem;
                            line-height: 2.45rem;
                            text-align: center;
                            text-decoration: none;
                        }
                    }
                }
            }
        }
        .call-cons-rt{
            width: 16.15rem;
            height: 17.1rem;
            display: inline-block;
            vertical-align: middle;
            background: url('../img/rt-cons-bg.png') no-repeat;
            background-size: 100% 100%;
            .cons-rt-tit{
                ul{
                    li{
                        font-family: 'PingFangSC-Semibold';
                        font-size: 0.95rem;
                        color: #FF9F00;
                        letter-spacing: 0.0355rem;
                        line-height: 2.5rem;
                        text-align: center;
                    }
                }
            }
            .bd{
                width: 16.15rem;
                height: 14.7rem;
                .tempWrap{
                    height: 2.45rem !important;
                }
                ul{
                    li{
                        a{
                            display: inline-block;
                            width: 45%;
                            font-family: 'PingFangSC-Semibold';
                            font-size: 0.9rem;
                            color: #FF9F00;
                            letter-spacing: 0.0338rem;
                            line-height: 2.45rem;
                            text-align: center;
                            text-decoration: none;
                        }
                    }
                }
            }
        }
    }
    .call-bots{
        width: 45rem;
        height: 2.25rem;
        display: block;
        vertical-align: middle;
        background: url('../img/cons-bot-bg.png') no-repeat;
        background-size: 100% 100%;
        margin: 0.35rem auto 0 auto;
        p{
            font-family: 'PingFangSC-Semibold';
            font-size: 0.95rem;
            color: #74D8FF;
            letter-spacing: 0.0355rem;
            line-height: 2.25rem;
            text-align: center;
        }
    }
} 