.gfleft-wraper {
    width: 10.0rem;
    height: 17.25rem;
    /* margin-top: 0.45rem; */
    margin-left: 0.4rem;
    .gfleft-up {
        width: 10.0rem;
        height: 6.625rem;
        margin-bottom: 0.35rem;
        background: rgba(52, 126, 212, 0.12);
        box-sizing: border-box;
        border: 1px solid rgba(9, 185, 255, 0.35);
        box-shadow: inset 0 0 0.3rem 0.1rem rgba(74, 144, 226, 0.39);
        .gf-icon1 {
            background: url("../img/gfuptt.png") no-repeat center;
            background-size: 100% 100%;
            width: 0.475rem;
            height: 0.5rem;
            display: inline-block;
            margin-left: 0.425rem;
            margin-top: 0.275rem;
        }
        .gfup-cont {
            width: 10.0rem;
            height: 5.625rem;
            .gf-bar {
                width: 100%;
                height: 1.375rem;
                margin-top: 0.375rem;
                font-size: 0;
                .gf-box1 {
                    font-family: 'reducto_condensed_ssiCn';
                    width: 3.675rem;
                    height: 1.375rem;
                    display: inline-block;
                    vertical-align: top;
                    em {
                        font-size: 0.75rem;
                        color: #2BBAEB;
                        letter-spacing: 0;
                        text-align: left;
                        line-height: 0.75rem;
                        display: inline-block;
                        margin-left: 0.55rem;
                    }
                    p {
                        font-family: PingFangSC-Regular;
                        font-size: 0.35rem;
                        color: #FFFFFF;
                        letter-spacing: 0;
                        text-align: left;
                        line-height: 0.625rem;
                        display: inline-block;
                        margin-left: 0.55rem;
                    }
                }
                .v-line {
                    width: 0.025rem;
                    height: 0.875rem;
                    background: #FFFFFF;
                    margin: 0.25rem 0;
                }
                .gf-box2 {
                    width: 4.525rem;
                    height: 1.375rem;
                    display: inline-block;
                    vertical-align: top;
                    em {
                        font-family: 'reducto_condensed_ssiCn';
                        font-size: 0.75rem;
                        color: #2BBAEB;
                        letter-spacing: 0;
                        text-align: left;
                        display: inline-block;
                        line-height: 0.75rem;
                        margin-left: 0.825rem;
                    }
                    p {
                        font-family: PingFangSC-Regular;
                        font-size: 0.35rem;
                        color: #FFFFFF;
                        letter-spacing: 0;
                        text-align: left;
                        display: inline-block;
                        line-height: 0.625rem;
                        margin-left: 0.825rem;
                    }
                }
            }
            i {
                display: inline-block;
                margin-left: 0.4rem;
                margin-bottom: 0.25rem;
            }
            .gf-icon2 {
                width: 0.9rem;
                height: 0.9rem;
                background: url("../img/gficon.png") no-repeat;
                background-size: 100% 100%;
            }
            .gf-icon3 {
                width: 0.9rem;
                height: 0.9rem;
                background: url("../img/gfup2.png") no-repeat;
                background-size: 100% 100%;
            }
            .gf-icon4 {
                width: 0.9rem;
                height: 0.9rem;
                background: url("../img/gfup3.png") no-repeat;
                background-size: 100% 100%;
            }
        }
    }
    .gfleft-down {
        width: 10.0rem;
        height: 10.275rem;
        background: rgba(52, 126, 212, 0.12);
        box-sizing: border-box;
        border: 1px solid rgba(9, 185, 255, 0.20);
        box-shadow: inset 0 0 0.3rem 0.1rem rgba(74, 144, 226, 0.39);
        i {
            background: url("../img/gfdown.png");
            background-size: 100% 100%;
            width: 0.5rem;
            height: 0.375rem;
            display: inline-block;
            margin-left: 0.4rem;
            margin-top: 0.325rem;
        }
        .gfdown-cont {
            width: 10.0rem;
            height: 9.075rem;
            margin-top: 0.125rem;
            .gf-header1 {
                width: 9.825rem;
                height: 1.0rem;
                margin: 0 auto;
                background: rgba(63, 133, 244, 0.18);
                font-size: 0;
            }
            .gf-header2 {
                width: 9.825rem;
                height: 1.0rem;
                margin: 0 auto;
                font-size: 0;
                border-bottom: 0.025rem solid rgba(235, 247, 252, 0.32);
            }
            .gf-header3 {
                width: 9.825rem;
                height: 1.0rem;
                margin: 0 auto;
                font-size: 0;
            }
            .gf-name {
                width: 1.55rem;
                height: 1.0rem;
                display: inline-block;
                text-align: center;
            }
            .gf-types {
                width: 1.75rem;
                height: 1.0rem;
                display: inline-block;
                text-align: center;
            }
            .gf-time1 {
                width: 2.8rem;
                height: 1.0rem;
                display: inline-block;
                text-align: center;
            }
            .gf-time2 {
                width: 3.7rem;
                height: 1.0rem;
                display: inline-block;
                text-align: center;
            }
            h4 {
                ont-family: PingFangSC-Regular;
                font-size: 0.35rem;
                color: #FFFFFF;
                letter-spacing: 0;
                line-height: 1.0rem;
            }
        }
    }
    .gf-title {
        width: 10.0rem;
        height: 1.0rem;
        top: -1px;
        left: -1px;
        position: relative;
        background-image: linear-gradient(180deg, rgba(74, 144, 226, 0.40) 0%, rgba(74, 144, 226, 0.00) 51%, rgba(74, 144, 226, 0.40) 100%);
        box-shadow: inset 0 0.025rem 0.075rem 0 #4A90E2;
        border-bottom: 0.025rem solid #2BBAEB;
        display: inline-block;
        h3 {
            font-family: PingFangSC-Medium;
            font-size: 0.375rem;
            color: #E1F7FF;
            letter-spacing: 0;
            text-align: left;
            display: inline-block;
            line-height: 1.0rem;
            vertical-align: top;
            margin-left: 0.35rem;
        }
        p {
            font-family: PingFangSC-Regular;
            font-size: 0.3rem;
            color: #FFFFFF;
            letter-spacing: 0;
            text-align: left;
            line-height: 1.0rem;
            float: right;
            margin-right: 0.25rem;
            cursor: pointer;
        }
    }
}