.touch_wraper {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url(../img/touch_bg.png) no-repeat center;
    background-size: 100% 100%;
    .touch_header {
        .touch_header_l {
            float: left;
            margin: 23px 0 0 25px;
            width: 320px;
            height: 55px;
            background: url(../img/touch_header_l.png) no-repeat center;
            background-size: 100% 100%;
        }
        .touch_header_r {
            margin: 21px 30px 0 0;
            float: right;
            font-size: 0;
            .touch_time {
                display: inline-block;
                vertical-align: middle;
                font-size: 40px;
                color: #FFFFFF;
                letter-spacing: 1.5px;
            }
            .touch_date {
                display: inline-block;
                vertical-align: middle;
                margin-left: 12px;
                p {
                    font-size: 14px;
                    color: #FFFFFF;
                    letter-spacing: 0.52px;
                }
            }
            .touch_weather {
                display: inline-block;
                vertical-align: middle;
                margin-left: 12px;
                .weather_icon {
                    display: inline-block;
                    vertical-align: middle;
                    width: 40px;
                    height: 28px;
                    background: url(../img/weather_icon.png) no-repeat center;
                    background-size: 100% 100%;
                }
                .touch_weather_r {
                    margin-left: 6px;
                    display: inline-block;
                    vertical-align: middle;
                    font-size: 14px;
                    color: #FFFFFF;
                    letter-spacing: 0.52px;
                }
            }
        }
    }
    .touch_content {
        margin: 0 auto;
        margin-top: 38px;
        width: 900px;
        height: 538px;
        background: url(../img/touch_content.png) no-repeat center;
        background-size: 100% 100%;
        .touch_content_header {
            margin: 0 auto;
            width: 867px;
            font-size: 38px;
            text-align: center;
            line-height: 75px;
            font-weight: bold;
            color: #074F66;
            letter-spacing: 1.42px;
            text-shadow: 0 1px 1px rgba(255, 255, 255, 0.50);
            border-bottom: 1px solid #1E8EB3;
        }
        .touch_content_con {
            .touch_l {
                float: left;
                margin-left: 60px;
                div {
                    overflow: hidden;
                    margin-top: 80px;
                    background: #FFFFFF;
                    border: 1px solid #50C3EC;
                    border-radius: 2px;
                    border-radius: 2px;
                    outline: none;
                    width: 356px;
                    height: 64px;
                    font-size: 30px;
                    color: #074F66;
                    letter-spacing: 1.12px;
                    padding: 0 13px;
                    box-sizing: border-box;
                    line-height: 64px;
                }
                span {
                    margin-top: 16px;
                    display: block;
                    font-size: 20px;
                    color: #074F66;
                    letter-spacing: 0.75px;
                }
                p {
                    margin-top: 30px;
                    width: 353px;
                    font-size: 20px;
                    color: #6E4500;
                    letter-spacing: 0.75px;
                    line-height: 30px;
                }
            }
            .touch_r {
                margin: 66px 63px 0 0;
                float: right;
                width: 372px;
                height: 324px;
                background: rgba(74, 144, 226, 0.15);
                border-radius: 4px;
                ul {
                    box-sizing: border-box;
                    li {
                        margin: 10px 0 0 10px;
                        float: left;
                        width: 79px;
                        height: 71px;
                        background-image: linear-gradient(180deg, #DBF9FF 0%, #3BD1FF 54%, #4DC2FF 100%);
                        border: 1px solid #4DA7C7;
                        box-shadow: 0 0 5px 0 rgba(1, 88, 104, 0.54), inset 0 0 6px 2px rgba(255, 255, 255, 0.50);
                        border-radius: 5px;
                        border-radius: 5px;
                        text-align: center;
                        line-height: 71px;
                        font-size: 48px;
                        color: #074F66;
                        letter-spacing: 1.8px;
                        font-weight: bold;
                        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.50);
                        .del_btn {
                            display: block;
                            margin: 0 auto;
                            margin-top: 24px;
                            width: 35px;
                            height: 26px;
                            background: url(../img/del_btn.png) no-repeat center;
                            background-size: 100% 100%;
                        }
                    }
                    .li_mouse {
                        background-image: linear-gradient(180deg, #4DC2FF 0%, #44BCD3 100%);
                    }
                    .btn_li {
                        width: 170px;
                        height: 54px;
                        line-height: 54px;
                        font-size: 24px;
                        letter-spacing: 0.9px;
                    }
                }
            }
        }
        .touch_content_con2 {
            font-weight: bold;
            .touch_content_text {
                margin: 10px 0 0 245px;
                div {
                    font-size: 28px;
                    color: #074F66;
                    letter-spacing: 1.05px;
                    line-height: 46px;
                    span {
                        display: inline-block;
                        width: 148px;
                    }
                    p {
                        margin-left: 92px;
                        display: inline-block;
                    }
                }
            }
            .touch_content_btn {
                margin: 12px 0 0 245px;
                div {
                    float: left;
                    width: 170px;
                    height: 54px;
                    background-image: linear-gradient(180deg, #DBF9FF 0%, #3BD1FF 54%, #4DC2FF 100%);
                    border: 1px solid #4DA7C7;
                    box-shadow: 0 0 5px 0 rgba(1, 88, 104, 0.54), inset 0 0 6px 2px rgba(255, 255, 255, 0.50);
                    border-radius: 2px;
                    border-radius: 2px;
                    font-size: 24px;
                    color: #074F66;
                    letter-spacing: 0.9px;
                    text-align: center;
                    line-height: 54px;
                }
                .yes_btn {
                    margin-left: 70px;
                }
            }
        }
    }
}