.wyg-content {
  width: 10rem;
  padding: 0 0.4rem 0 0.4rem;
  .section1 {
    background: url(../img/section1-bg.png) no-repeat;
    background-size: 100% 100%;
    width: 10rem;
    height: 7.05rem;
    display: inline-block;
    .public-header {
      background: url(../img/public-headerbg.png) no-repeat;
      background-size: 100% 100%;
      width: 10rem;
      height: 1rem;
      display: inline-block;
      i {
        background: url(../img/icon1.png) no-repeat;
        background-size: 100% 100%;
        width: 0.5rem;
        height: 0.5rem;
        display: inline-block;
        vertical-align: middle;
        margin-left: 0.4rem;
      }
      span {
        display: inline-block;
        vertical-align: middle;
        font-family: PingFangSC-Medium;
        font-size: 0.375rem;
        color: #e1f7ff;
        line-height: 1rem;
        margin-left: 0.4rem;
      }
    }
    #echarts1 {
      width: 6.5rem;
      height: 5.425rem;
      float: left;
      margin-top: .8rem;
      margin-left: 0.5rem;
    }
    .text {
      float: right;
      margin-right: .2rem;
      li {
        font-family: PingFangSC-Regular;
        font-size: 0.35rem;
        color: #ffffff;
        margin-top: -0.1rem;
        text-align: center;
        em {
          font-family: 'reducto_condensed_ssiCn';
          font-size: 0.75rem;
          color: #2bbaeb;
          margin-left: .25rem;
          margin-right: 0.2rem;
        }
      }
    }
  }
  .section2 {
    background: url(../img/section2-bg.png) no-repeat;
    background-size: 100% 100%;
    width: 10rem;
    height: 4.75rem;
    display: inline-block;
    margin-top: 0.375rem;
    .public-header {
      background: url(../img/public-headerbg.png) no-repeat;
      background-size: 100% 100%;
      width: 10rem;
      height: 1rem;
      display: inline-block;
      i {
        background: url(../img/icon2.png) no-repeat;
        background-size: 100% 100%;
        width: 0.475rem;
        height: 0.5rem;
        display: inline-block;
        vertical-align: middle;
        margin-left: 0.4rem;
      }
      span {
        display: inline-block;
        vertical-align: middle;
        font-family: PingFangSC-Medium;
        font-size: 0.375rem;
        color: #e1f7ff;
        line-height: 1rem;
        margin-left: 0.4rem;
      }
      .date {
        float: right;
        display: inline-block;
        margin-right: 0.5rem;
        margin-top: 0.25rem;
        li {
          display: inline-block;
          font-size: 0.3rem;
          color: #e1f7ff;
          width: 0.95rem;
          height: 0.5rem;
          line-height: 0.5rem;
          text-align: center;
          border: 1px solid rgba(0,0,0,0);
          cursor: pointer;
        }
        li:nth-child(2){
          margin-left: 0.2rem;
        }
        .active {
          border: 1px solid #2bbaeb;
        }
      }
    }
    #echarts2 {
      width: 9.075rem;
      height: 3.35rem;
      margin-left: .5rem;
    }
  }
  .section3 {
    background: url(../img/section3-bg.png) no-repeat;
    background-size: 100% 100%;
    width: 10rem;
    height: 4.75rem;
    display: inline-block;
    margin-top: 0.375rem;
    .public-header {
      background: url(../img/public-headerbg.png) no-repeat;
      background-size: 100% 100%;
      width: 10rem;
      height: 1rem;
      display: inline-block;
      i {
        background: url(../img/icon3.png) no-repeat;
        background-size: 100% 100%;
        width: 0.5rem;
        height: 0.3rem;
        display: inline-block;
        vertical-align: middle;
        margin-left: 0.4rem;
      }
      span {
        display: inline-block;
        vertical-align: middle;
        font-family: PingFangSC-Medium;
        font-size: 0.375rem;
        color: #e1f7ff;
        line-height: 1rem;
        margin-left: 0.4rem;
      }
      .date {
        float: right;
        display: inline-block;
        margin-right: .5rem;
        margin-top: 0.25rem;
        li {
          display: inline-block;
          font-size: 0.3rem;
          color: #e1f7ff;
          width: 0.95rem;
          height: 0.5rem;
          line-height: 0.5rem;
          text-align: center;
          border: 1px solid rgba(0,0,0,0);
          cursor: pointer;
        }
        li:nth-child(2){
          margin-left: 0.2rem;
        }
        .active {
          border: 1px solid #2bbaeb;
        }
      }
    }
    #echarts3 {
      width: 9.45rem;
      height: 3.325rem;
      margin-left: .3rem;
    }
  }
}
